.기술특징Section7 {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px; /* 컨테이너 간의 간격 */
  padding: 20px;
}

.기술특징ContainerLeft {
  width: 84%;
  align-self: flex-start; /* 왼쪽 정렬 */
  border: 1px solid yellow; /* 레이아웃 확인용 */
  padding: 20px;
}

.기술특징ContainerRight {
  width: 73%;
  align-self: flex-end; /* 오른쪽 정렬 */
  border: 1px solid yellow; /* 레이아웃 확인용 */
  padding: 20px;
}

h2 {
  margin: 0 0 10px 0; /* 소제목 여백 */
  font-size: 5rem; /* 소제목 글자 크기 */
}

ul {
  list-style-type: disc; /* 리스트 스타일 */
  margin: 0;
  padding-left: 20px; /* 리스트 안쪽 여백 */
  font-size: 2rem; /* 리스트의 글자 크기 */
}

li {
  margin-bottom: 8px; /* 리스트 항목 간의 간격 */
}

.핵심Container {
  display: flex;
  align-items: center; /* 아이콘과 텍스트를 수평으로 정렬 */
  justify-content: center; /* 부모 컨테이너 안에서 가운데 정렬 */
  gap: 15px; /* 아이콘과 텍스트 간의 간격 */
  width: 80%; /* 컨테이너 너비 */
  background-color: #5e5e5e; /* 배경색 */
  border-radius: 10px; /* 둥근 모서리 */
  padding: 20px; /* 내부 여백 */
  color: white; /* 글자 색상 */
  margin: 0 auto; /* 좌우 가운데 정렬 */
}

.아이콘 {
  width: 50px; /* 아이콘 너비 */
  height: 50px; /* 아이콘 높이 */
  flex-shrink: 0; /* 아이콘 크기 고정 */
  margin-left: 30px;
}

.핵심텍스트 p{
  display: flex;
  flex-direction: column; /* 텍스트를 세로로 정렬 */
  font-size: 3rem; /* 기본 글자 크기 */
  font-weight: 700;
}